import axios from 'axios'
import { API } from '../../util/constant/constant';
const PATH = API.deliver;
const service = {
    /**
     *    -    
     * 
     * @author Dong-Min Seol
     * @since  2019.09.22
     */
    registerGoodsRelease: (data) => {
        return axios.post(`${PATH}/sendGoods_packaging`, data)
            .then(res => {
                return res.status == 200 ? true : false;
            })
    },

    /**
     *    -    
     * 
     * @author Dong-Min Seol
     * @since  2019.09.22
     */
    registerGoodsPackage: (data) => {
        return axios.post(`${PATH}/enrollGoods_packaging`, data)
            .then(res => {
                return res.status == 200 ? true : false;
            })
    },
    /**
     *    -    
     * 
     * @author Dong-Min Seol
     * @since  2019.09.22
     */
    registerGoodsArrive: (data) => {
        return axios.post(`${PATH}/enrollArriveTime_packaging`, data)
            .then(res => {
                return res.status == 200 ? true : false;
            });
    }

}

export default service;